"use client"

import Image from 'next/image';
import Link from 'next/link';
import Slider from '../ui/Slider';
import { useEffect, useState } from 'react';
import { getProductsWithoutPagination } from '@/services/productService';
import { baseUri } from '@/services/constant';


export type Product = {
  slug: string;
  id: string;
  title: string;
  description: string;
  category: { name: string; slug: string };
  images: string[];
};

export type Category = {
  title: string;
  items: { id: string; name: string; image: string; slug: string }[];
};
const ProductsSection = () => {

  const [products, setProducts] = useState<Product[]>([])

  useEffect(() => {
    async function fetchData(){
      try{
        const resp = await getProductsWithoutPagination()
        setProducts(resp.data)
      }
      catch (err) {
        console.error(err);
      }
  }
  fetchData()
  }, [])
  

return (
  <section className="py-5 md:py-20 bg-[#FFE8DF]">
    <div className="container mx-auto px-4">
      <div className="text-center mb-3 md:mb-8">
        <h2 className="text-xl lg:text-4xl text-[#1E1410] font-thin mb-2">Featured Products</h2>
        <p className="text-sm md:text-lg text-[#1E1410] uppercase tracking-[1px] px-2">OUR BEST-IN-CLASS SECURITY PRODUCTS</p>
      </div>

      <div>
        <Slider
          autoPlay={true}
          autoPlayInterval={2000}
          showArrows={false}
          showDots={false}
          slidesToShow={3.4}
          className='h-full'
          responsive={[
            {
              breakpoint: 992,
              showDots: false,
              slidesToShow: 2
            }
          ]}
        >
          {products &&  products.map((product ,  index) => (
            <div
              key={product.id}
              className="rounded-lg sm:rounded-3xl p-3 sm:p-6 md:p-8 transition-transform mx-2 md:mx-4"
              style={{
                background: index % 2 === 0 ?
                  'linear-gradient(to bottom, #FFCC33, #FFB347)' :
                  'linear-gradient(to bottom, #FF7F50, #FF6347)',
              }}
            >
              <div className="flex flex-col">
                <div className="relative h-30 sm:h-80 w-full mb-4 bg-white rounded-lg sm:rounded-xl pt-2 sm:pt-12  flex align-middle justify-center">
                  <Image
                    src={`${baseUri}${product.images[0]}` }
                    alt={product.title}
                    width={0}
                    height={0}
                    unoptimized
                    className='h-[200] w-[200] object-contain'
                  />
                </div>
                <div style={{
                  color: index % 2 === 0 ? '#000' : '#fff'
                }}>
                  <p className="text-xs sm:text-sm md:text-lg font-light">{product.category?.name}</p>
                  <h3 className="text-sm sm:text-lg md:text-2xl font-thin tracking-[0.5px] md:tracking-[1px]">{product.title}</h3>
                </div>
              </div>
            </div>
          ))}
        </Slider>
      </div>


      <div className="text-center mt-3 sm:mt-10">
        <Link
          href="/products"
          className="inline-flex items-center bg-orange-500 text-sm sm:text-lg text-white px-2 sm:px-6 py-1 sm:py-2 rounded-md font-medium hover:bg-orange-600 transition"
        >
          VIEW ALL
          <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 ml-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14 5l7 7m0 0l-7 7m7-7H3" />
          </svg>
        </Link>
      </div>
    </div>
  </section>
);
};

export default ProductsSection;